@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&family=Ubuntu&display=swap);
body{margin:0;padding:0;overflow-y:hidden;overflow-x:hidden;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{margin:0;padding:0;box-sizing:border-box;font-family:"Montserrat", sans-serif}#app-container{overflow:hidden}.windowBar{width:100%;height:2.5rem;background-color:#776d87;display:flex;align-items:center;justify-content:flex-end}.windowBarAdvanced{width:100%;height:2.5rem;background-color:#776d87;display:flex;align-items:center;justify-content:space-between}.windowButtonsContainer{display:flex;align-items:center}.windowButtons{height:.8rem;width:.8rem;border-radius:360px;margin:.2rem;cursor:pointer}.minimize{background-color:#f9f871}.maximize{background-color:#00c9a4}.close{background-color:#e18999;margin-right:.5rem}.maximized{top:0;left:0;height:95vh;width:100vw;position:absolute;display:flex;flex-direction:column;align-items:center;z-index:5}@media only screen and (max-width: 600px){.minimize{display:none}.maximize{display:none}.windowButtons{height:1.2rem;width:1.2rem}}

#taskbar-container{background-color:red}#taskbar{display:flex;flex-direction:row;justify-content:space-between;box-shadow:0px 0px 4px 3px rgba(0,0,0,0.25);align-items:center;background-color:rgba(74,68,83,0.85);width:100vw;height:5vh;position:absolute;bottom:0;left:0;z-index:7}#start-container{height:100%;width:20%;display:flex;align-items:center;justify-content:flex-start}#startMenuButton{margin-left:.5rem;height:100%;width:auto}#taskbarTime{padding:.4rem}#taskbarApps{display:flex;height:90%}#taskbarApps img{margin-left:.5rem;margin-right:.5rem}#time-container p{font-size:1rem;font-family:"Montserrat", sans-serif;font-weight:600;color:#dadada;text-align:right;margin-right:.5rem}#time-container{width:20%}@media only screen and (max-width: 600px){#startMenuButton{display:none}#time-container p{display:none}}

#startMenu{display:flex;flex-direction:column;justify-content:flex-start;align-items:flex-start;position:absolute;bottom:6vh;left:.5rem;background-color:rgba(74,68,83,0.85);height:52vh;width:20vw;z-index:7;border-radius:10px;box-shadow:0px 0px 4px 3px rgba(0,0,0,0.25)}.startApps{display:flex;flex-direction:row;align-items:center;width:35%;height:10%;margin:.5rem;cursor:pointer}.startApps img{height:100%;width:auto;margin-right:.5rem;margin-left:.5rem}.startApps p{color:#dadada;font-size:.8rem;font-family:"Montserrat", sans-serif;font-weight:600}

::-webkit-scrollbar{width:0px}.terminal{width:35vw;height:45vh;overflow:hidden;display:flex;flex-direction:column;align-items:center;resize:both;box-shadow:0px 0px 4px 3px rgba(0,0,0,0.25);position:absolute;top:30%;left:30%}.terminal-focused{width:35vw;height:45vh;overflow:hidden;display:flex;flex-direction:column;align-items:center;resize:both;box-shadow:0px 0px 4px 3px rgba(0,0,0,0.25);position:absolute;top:30%;left:30%;z-index:6}

#desktop{display:grid;grid-template-columns:repeat(8, 1fr);position:absolute;width:100vw;height:100vh;top:0;left:0;z-index:-3;background-image:url(/static/media/background.139eeaf9.jpg);background-size:cover}#shortcutContainer{display:flex;flex-direction:column;width:100%;height:100%;justify-content:flex-start;align-items:center}.shortcut{display:flex;flex-direction:column;align-items:center;justify-content:center;height:10%;width:100%}.shortcut img{height:45%;width:auto;margin-bottom:.2rem;box-shadow:0px 0px 4px 3px rgba(0,0,0,0.25)}.shortcut a{text-decoration:none;width:30%;height:45%;text-align:center;color:white;margin-bottom:.2rem}#github img{height:100%;width:60%;box-shadow:none}.shortcut p{background-color:rgba(74,68,83,0.85);color:white;border-radius:5px;padding:.3rem;font-size:.75rem;color:#dadada;font-family:"Montserrat", sans-serif;font-weight:600}@media only screen and (max-width: 600px){#github img{height:90%;width:100%}#desktop{top:2vh;height:98vh;grid-template-columns:repeat(4, 1fr)}}

.browser{width:70vw;height:80vh;overflow:hidden;display:flex;flex-direction:column;align-items:center;resize:both;box-shadow:0px 0px 4px 3px rgba(0,0,0,0.25);position:absolute}.browser-focused{width:70vw;height:80vh;overflow:hidden;display:flex;flex-direction:column;align-items:center;resize:both;box-shadow:0px 0px 4px 3px rgba(0,0,0,0.25);z-index:6;position:absolute}#webPage{width:100%;height:calc(100% - 2.5rem);border:none}

#mobile-bar{position:absolute;top:0;left:0;width:100vw;height:2vh;background-color:#1b1b1b;opacity:0.9;display:flex;flex-direction:row;align-items:center;justify-content:space-between}#mobileTime{font-size:0.7rem;font-family:"Montserrat", sans-serif;font-weight:600;color:#dadada;text-align:right;margin-left:0.3rem}.mobile-icons{color:#dadada;font-size:0.7rem}#mobile-icon-container{display:flex;margin-right:.3rem;align-items:center}#mobile-icon-container p{font-size:0.7rem;font-family:"Montserrat", sans-serif;font-weight:600;color:#dadada;text-align:right;margin-left:0.3rem}

#notes{width:100%;height:calc(100% - 2.5rem);border:none}textarea:focus{outline:none}.notepad{width:40vw;height:80vh;overflow:hidden;display:flex;flex-direction:column;align-items:center;resize:both;box-shadow:0px 0px 4px 3px rgba(0,0,0,0.25);position:absolute;top:15%;left:15%;z-index:1}.notepad-focused{width:40vw;height:80vh;overflow:hidden;display:flex;flex-direction:column;align-items:center;resize:both;box-shadow:0px 0px 4px 3px rgba(0,0,0,0.25);z-index:6;position:absolute;top:7%;left:30%}textarea{padding:2rem;background-color:#dadada;color:#1b1b1b;font-size:2rem;font-family:"Montserrat", sans-serif}#saveButton{color:#dadada;cursor:pointer;margin-left:.2rem}

.resume{width:70vw;height:80vh;overflow:hidden;display:flex;flex-direction:column;align-items:center;resize:both;box-shadow:0px 0px 4px 3px rgba(0,0,0,0.25);position:absolute;top:10%;left:15%}.resume-focused{width:70vw;height:80vh;overflow:hidden;display:flex;flex-direction:column;align-items:center;resize:both;box-shadow:0px 0px 4px 3px rgba(0,0,0,0.25);z-index:6;position:absolute;top:10%;left:15%}#resumePage{width:100%;height:calc(100% - 2.5rem);border:none}

.music-player{width:50vw;height:60vh;overflow:hidden;display:flex;flex-direction:column;align-items:center;resize:both;box-shadow:0px 0px 4px 3px rgba(0,0,0,0.25);position:absolute;top:10%;left:15%}.music-player-focused{width:50vw;height:60vh;overflow:hidden;display:flex;flex-direction:column;align-items:center;resize:both;box-shadow:0px 0px 4px 3px rgba(0,0,0,0.25);z-index:6;position:absolute;top:10%;left:15%}#music-player{width:100%;height:calc(100% - 2.5rem);display:flex;background-color:#4a4453}#gallery{width:50%;height:100%;display:flex;flex-direction:column;align-items:flex-start;justify-content:center}#album-cover{width:auto;height:90%;margin-left:10%;border-radius:10px;box-shadow:0px 0px 4px 3px rgba(0,0,0,0.25)}#lyrics-controls{display:flex;flex-direction:column;align-items:center;justify-content:space-around;width:50%;height:100%}#lyrics{height:70%;overflow:hidden}#lyrics p{color:#dadada;font-family:"Montserrat";font-weight:600;text-shadow:0px 0px 4px 3px rgba(0,0,0,0.25);text-align:center;-webkit-animation-name:slide;animation-name:slide;-webkit-animation-duration:210s;animation-duration:210s;-webkit-animation-delay:13s;animation-delay:13s}@-webkit-keyframes slide{from{margin-top:0}to{margin-top:-60rem}}@keyframes slide{from{margin-top:0}to{margin-top:-60rem}}#controls span{color:#dadada;font-size:3rem;text-shadow:0px 0px 4px 3px rgba(0,0,0,0.25);cursor:pointer}

#backButton{color:#dadada;cursor:pointer}#max-file{background-color:#dadada;color:#1b1b1b}.file-manager{width:35vw;height:45vh;overflow:hidden;flex-direction:column;align-items:center;resize:both;box-shadow:0px 0px 4px 3px rgba(0,0,0,0.25);position:absolute;top:30%;left:30%;background-color:#dadada;color:#1b1b1b}.file-manager-focused{width:35vw;height:45vh;overflow:hidden;flex-direction:column;align-items:center;resize:both;box-shadow:0px 0px 4px 3px rgba(0,0,0,0.25);position:absolute;top:30%;left:30%;background-color:#dadada;color:#1b1b1b;z-index:6}#file-view{display:grid;grid-template-columns:repeat(6, 1fr);grid-template-rows:repeat(6, 1fr);height:calc(100% - 2.5rem);width:100%}.file{display:flex;flex-direction:column;align-items:center;justify-content:center;width:100%;height:100%}.file img{width:70%;height:auto}.file p{font-weight:400;font-family:"Ubuntu", sans-serif;color:#1b1b1b}

