$mediumShadow: 0px 0px 4px 3px rgba(0, 0, 0, 0.25);
$lightPurple: #776d87;
$darkColor: #4A4453;
$trafficRed: #e18999;
$trafficYellow: #f9f871;
$trafficGreen: #00c9a4;
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&family=Ubuntu&display=swap');

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "Montserrat", sans-serif;	
}

#app-container {
	overflow:hidden;
}

.windowBar {
	width: 100%;
	height:2.5rem;
	background-color: $lightPurple;
	display: flex;
	align-items: center;
	justify-content: flex-end;
}

.windowBarAdvanced {
	width: 100%;
	height:2.5rem;
	background-color: $lightPurple;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.windowButtonsContainer {
	display: flex;
	align-items: center;
}

.windowButtons {
	height: .8rem;
	width:.8rem;
	border-radius: 360px;
	margin:.2rem;
	cursor: pointer;
}

.minimize {
	background-color: $trafficYellow;
}

.maximize {
	background-color: $trafficGreen;
}

.close {
	background-color: $trafficRed;
	margin-right: .5rem;
}

.maximized {
	top: 0;
	left:0;
	height: 95vh;
	width: 100vw;
	position:absolute;
	display: flex;
	flex-direction: column;
	align-items: center;
	z-index:5;
}

@media only screen and (max-width:600px) {
	
	.minimize {
		display: none;
	}

	.maximize {
		display: none;
	}
	
	.windowButtons {
		height:1.2rem;
		width: 1.2rem;
		}
}

